import React from "react";
import Thumbnail from "./Thumbnail";
import VideoInfo from "./VideoInfo";
import { motion } from "framer-motion";

export default function Download({ mediaInfo, thumbnail, videoId }) {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ delay: 1 }}
      className="media-info"
    >
      <Thumbnail thumbnail={thumbnail} />
      <VideoInfo mediaInfo={mediaInfo} videoId={videoId} />
    </motion.div>
  );
}
